<script>

import { mapState } from 'vuex';
import { login } from '@/utils/public';
import { DApp } from '@/utils/wallet';
import { accDiv } from '@/utils/public';
import moment from 'moment';
import NoSleep from 'nosleep.js';

export default {
	name: 'Town',

	components: {
	},

  data() {
    return {
      logined: false,
      tipMsg: '',
      checked: true,
      land: [],
      gemfactory: [],
      feedmill: [],
      bakery: [],
      cow: [],
      sheep: [],
      chicken: [],
      wheat: 0,
      corn: 0,
      carrot: 0,
      ruby: 0,
      beryl: 0,
      topaz: 0,
      cowfood: 0,
      chkfood: 0,
      shpfood: 0,
      milk: 0,
      egg: 0,
      wool: 0,
      bread: 0,
      cookie: 0,
      cake: 0,
      dcash: 0,
      orderLogs: [],
      works: '',
      price: [],
      delay: 5000,
      updateDelay: 5000,
      failDelay: 5000,
      failMax: 5,
      freeCPU: true,
      logMsg: [],
      showProduct: false,
      showCalc: false,
      requireAsset: '',
      createAsset: '',
      requireTime: '',
      endTime: ''
    }
  },
  computed: {
    ...mapState({
      account: state => state.app.account,
    }),
  },
  methods: {
    handleLogin() {
      login(this, () => {
        this.logined = true;
        //this.account.name = "dfsdeveloper"; //just for test
        this.handleUpdateTip();
        this.handleReset();
        this.handleGetAccNft();
        this.handleGetWorks();
        this.handleGetAccAssets();
        this.handleGetOrderLogs();
      })
    },
    handleReset() {
      this.land = [];
      this.gemfactory = [];
      this.feedmill = [];
      this.bakery = [];
      this.cow = [];
      this.sheep = [];
      this.chicken = [];
      this.wheat = 0;
      this.corn = 0;
      this.carrot = 0;
      this.ruby = 0;
      this.beryl = 0;
      this.topaz = 0;
      this.cowfood = 0;
      this.chkfood = 0;
      this.shpfood = 0;
      this.milk = 0;
      this.egg = 0;
      this.wool = 0;
      this.bread = 0;
      this.cookie = 0;
      this.cake = 0;
      this.dcash = 0;
    },
    handleNoPasswordTx() {
      if (typeof this.account.name === 'undefined') {
        this.$toast({type: 'fail', message: "",})
        return;
      } 
      this.$dialog.confirm({
        title: '',
        message: 'Start',
      }).then(() => {
        const name = this.account.name;
        const permission = this.account.permissions;
        const params = {
          actions: [{
            account: 'dfsfarmitems',
            name: 'transfer',
            authorization: [{
              actor: name,
              permission,
            }],
            data: {
              from: name,
              to: 'davidaccount',
              quantity: '0.0001 DCASH',
              memo: 'tip',
            }          
          }]
        }
        DApp.toTransaction(params, (err) => {
          err = this.handleDealWithError(err)
          if (err && err.code == 402) {
            this.$toast({
              type: 'fail',
              message: err.message,
            })
            this.logMsg.push("handleNoPasswordTx tx error");
            this.logMsg.push(err.message);
            return;
          }
          if (err) {
            this.$toast({
              type: 'fail',
              message: err.message,
            })
            this.logMsg.push("handleNoPasswordTx tx error");
            this.logMsg.push(err.message);
            return;
          }
          this.$toast({
            type: 'success',
            message: 'Success'
          })
        })  
      });     
    },
    handleUpdateTip() {
      if (typeof this.account.name !== 'undefined') {
        this.tipMsg = ''
        this.logined = true;
      } else {
        this.tipMsg = ''
        this.logined = false;
      }
    },
    handleDealWithError(err) {
      //CPU
      // {
      //     "code": "402",
      //     "message": "User rejected the signature request"
      // } 
      // {
      //     "code": 3050003,
      //     "message": "assertion failure with message: no balance object found"
      // }     
      //CPU
      // {
      //     "code": 999,
      //     "message": "{\"type\":\"signature_rejected\",\"message\":\"User rejected the signature request\",\"code\":402,\"isError\":true}"
      // }
      // {
      //     "code": 500,
      //     "msg": "{\"code\":500,\"message\":\"Internal Service Error\",\"error\":{\"code\":3050003,\"name\":\"eosio_assert_message_exception\",\"what\":\"eosio_assert_message assertion failure\",\"details\":[{\"message\":\"assertion failure with message: no balance object found\",\"file\":\"cf_system.cpp\",\"line_number\":14,\"method\":\"eosio_assert\"}]}}"
      // }
      if (err.message !== undefined) {
        if (this.isJSON(err.message)) {
          const content = JSON.parse(err.message)
          const code = content.code;
          const message = content.message;
          return {code, message}
        } else {
          return err;
        }
      } else if (err.msg !== undefined) {
        const content = JSON.parse(err.msg)
        const code = content.error.code;
        const message = content.error.details[0].message;
        return {code, message}
      } else {
        return err;
      }
    },
    isJSON(str) {
      if (typeof str == 'string') {
        try {
          var obj=JSON.parse(str);
          if(typeof obj == 'object' && obj ){
              return true;
          }else{
              return false;
          }
        } catch(e) {
          //console.log('error'+str+'!!!'+e);
          return false;
        }
      }
    },
    async handleGetAccAssets() {
      const params = {
        "code":"dfsfarmitems",
        "scope":this.account.name,
        "table":"accounts",
        //"lower_bound": "1000",
        "json":true,
      }
      const {status, result} = await this.$api.get_table_rows(params)
      if (!status) {
        this.$toast({type: 'fail', message: "",})
        return
      }
      const rows = result.rows || []
      if (!rows.length) {
        return
      }
      rows.forEach(elem => {
        const assets = elem.balance.split(' ');

        if (assets[1] === "WHEAT") {
          this.wheat = assets[0];
        } else if (assets[1] === "CORN") {
          this.corn = assets[0];
        } else if (assets[1] === "CARROT") {
          this.carrot = assets[0];
        } else if (assets[1] === "RUBY") {
          this.ruby = assets[0];
        } else if (assets[1] === "BERYL") {
          this.beryl = assets[0];
        } else if (assets[1] === "TOPAZ") {
          this.topaz = assets[0];
        } else if (assets[1] === "COWFOOD") {
          this.cowfood = assets[0];
        } else if (assets[1] === "CHKFOOD") {
          this.chkfood = assets[0];
        } else if (assets[1] === "SHPFOOD") {
          this.shpfood = assets[0];
        } else if (assets[1] === "MILK") {
          this.milk = assets[0];
        } else if (assets[1] === "EGG") {
          this.egg = assets[0];
        } else if (assets[1] === "WOOL") {
          this.wool = assets[0];
        } else if (assets[1] === "BREAD") {
          this.bread = assets[0];
        } else if (assets[1] === "COOKIE") {
          this.cookie = assets[0];
        } else if (assets[1] === "CAKE") {
          this.cake = assets[0];
        } else if (assets[1] === "DCASH") {
          this.dcash = assets[0];
        }
      }) 
    },
    async handleGetAccNft() {
      const params = {
        "code":"dfsnftassets",
        "scope":this.account.name,
        "table":"assets",
        "limit": 1000,
        "json":true,
      }
      const {status, result} = await this.$api.get_table_rows(params)
      if (!status) {
        this.$toast({type: 'fail', message: "NFT",})
        this.logMsg.push("NFT");
        return
      }
      const rows = result.rows || []
      if (!rows.length) {
        return
      }
      rows.forEach(elem => {
        elem.level = null;
        elem.task = {countDown: 0, exist: false}; //0finishauto-start="false"
        elem.multiple = 1;
        elem.round = 1;
        elem.workid = null;
        elem.autoSwitch = false;
        elem.failCount = 0;
        if (elem.collection === 'land') {
          this.land.push(elem);
        } else if (elem.collection === 'gemfactory') {
          this.gemfactory.push(elem);
        } else if (elem.collection === 'feedmill') {
          this.feedmill.push(elem);
        } else if (elem.collection === 'bakery') {
          this.bakery.push(elem);
        } else if (elem.collection === 'cow') {
          this.cow.push(elem);
        } else if (elem.collection === 'sheep') {
          this.sheep.push(elem);
        } else if (elem.collection === 'chicken') {
          this.chicken.push(elem);
        }
      });
      this.handleGetAllLevel();
      this.handleGetAllTask();
    },
    async handleGetOrderLogs() {
      const params = {
        "code":"dfsmetaverse",
        "scope":this.account.name,
        "table":"orderlogs",
        "limit": 1000,
        "json":true,
      }
      const {status, result} = await this.$api.get_table_rows(params)
      if (!status) {
        this.$toast({type: 'fail', message: "",})
        this.logMsg.push("");
        return
      }
      const rows = result.rows || []
      if (!rows.length) {
        return
      }
      this.orderLogs = rows;
    },
    async handleGetWorks() {
      const params = {
        "code":"dfsmetaverse",
        "scope":"dfsmetaverse",
        "table":"works",
        "limit": 1000,
        "json":true,
      }
      const {status, result} = await this.$api.get_table_rows(params)
      if (!status) {
        this.$toast({type: 'fail', message: "",})
        this.logMsg.push("");
        return
      }
      const rows = result.rows || []
      if (!rows.length) {
        return
      }
      this.works = rows;
    },
    async handleGetLevel(nft) {
      nft.forEach(async elem => {
        const params = {
          "code":"dfsmetaverse",
          "scope":elem.id,
          "table":"stats",
          "json":true,
        }
        const {status, result} = await this.$api.get_table_rows(params)
        if (!status) {
          this.$toast({type: 'fail', message: "NFT",})
          this.logMsg.push("NFT");
          return
        }
        const rows = result.rows || []
        if (!rows.length) {
          return
        }
        elem.level = rows[0].val;
        elem.multiple = elem.level;
      })
    },
    handleGetAllLevel() {
      this.handleGetLevel(this.land);
      this.handleGetLevel(this.gemfactory);
      this.handleGetLevel(this.feedmill);
      this.handleGetLevel(this.bakery);
      this.handleGetLevel(this.cow);
      this.handleGetLevel(this.sheep);
      this.handleGetLevel(this.chicken);
    },
    async handleGetTask(nft) {
      nft.forEach(async elem => {
        const params = {
          "code":"dfsmetaverse",
          "scope":"dfsmetaverse",
          "table":"tasks",
          "lower_bound": elem.id,
          "upper_bound": elem.id,
          "json":true,
        }
        const {status, result} = await this.$api.get_table_rows(params)
        if (!status) {
          this.$toast({type: 'fail', message: ""})
          this.logMsg.push("");
          return
        }
        const rows = result.rows || []
        if (!rows.length) {
          return
        }
        elem.task = rows[0];
        elem.task.create_time = moment(`${elem.task.create_time}.000+0000`).format();
        const mature_time = moment(`${elem.task.mature_time}.000+0000`);
        elem.task.mature_time = mature_time.format();
        elem.task.end_time = mature_time.format('YYYY-MM-DD HH:mm:ss');
        const countDown = mature_time.diff(moment());
        if (countDown <= 0) {
          elem.task.countDown = 0;
        } else {
          elem.task.countDown = countDown;
        }
        elem.task.exist = true;
      })
    },
    async handleUpdateTask(elem) {
      const params = {
        "code":"dfsmetaverse",
        "scope":"dfsmetaverse",
        "table":"tasks",
        "lower_bound": elem.id,
        "upper_bound": elem.id,
        "json":true,
      }
      const {status, result} = await this.$api.get_table_rows(params)
      if (!status) {
        this.$toast({type: 'fail', message: ""})
        this.logMsg.push("");
        return
      }
      const rows = result.rows || []
      if (!rows.length) {
        elem.task = {countDown: 0, exist: false};
        return
      }
      elem.task = rows[0];
      elem.task.create_time = moment(`${elem.task.create_time}.000+0000`).format();
      const mature_time = moment(`${elem.task.mature_time}.000+0000`);
      elem.task.mature_time = mature_time.format();
      elem.task.end_time = mature_time.format('YYYY-MM-DD HH:mm:ss');
      const countDown = mature_time.diff(moment());
      if (countDown <= 0) {
        elem.task.countDown = 0;
      } else {
        elem.task.countDown = countDown;
      }
      elem.task.exist = true;
    },
    handleGetAllTask() {
      this.handleGetTask(this.land);
      this.handleGetTask(this.gemfactory);
      this.handleGetTask(this.feedmill);
      this.handleGetTask(this.bakery);
      this.handleGetTask(this.cow);
      this.handleGetTask(this.sheep);
      this.handleGetTask(this.chicken);
    },
    handleHarvest(v) {
      if (v.task.exist === false) {
        this.$toast({type: 'fail', message: ""})
        this.logMsg.push("");
        return;        
      }
      if (moment(v.task.mature_time).diff(moment()) > 0) {
        this.$toast({type: 'fail', message: ""})  
        this.logMsg.push("");
        return; 
      }
      const name = this.account.name;
      const permission = this.account.permissions;
      const params = {
        actions: [{
          account: 'dfsmetaverse',
          name: 'harvest',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            taskid: v.id,
          }
        }]
      }
      DApp.toTransaction(params, (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleHarvest tx error");
          this.logMsg.push(err.message);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleHarvest tx error");
          this.logMsg.push(err.message);
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        if (!v.autoSwitch) {
          setTimeout(this.handleUpdateTask, this.updateDelay, v);
        }
      })
    },
    handleCalc(v) {
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: ""})
        this.logMsg.push("");
        return;        
      }
      if (this.works[workid-1].id != workid) {
        this.$toast({type: 'fail', message: "id"})
        this.logMsg.push("id");
        return;             
      }

      let requireAsset = this.works[workid-1].require_asset.slice(); //
      requireAsset.forEach((elem, i) => {
        let asset = elem.split(" ");
        asset[0] = (parseInt(asset[0]) * multiple * round).toFixed(4);
        requireAsset[i] = asset[0] + ' ' + asset[1]
      })
      let createAsset = this.works[workid-1].create_asset.slice(); //
      let asset = createAsset.split(" ");
      asset[0] = (parseInt(asset[0]) * multiple * round).toFixed(4);
      createAsset = asset[0] + ' ' + asset[1]

      let requireTime = this.works[workid-1].require_time * multiple * round;
      this.requireAsset = requireAsset;
      this.createAsset = createAsset;
      this.requireTime = requireTime / 60;
      this.endTime = moment().add(this.requireTime, 'm').format('YYYY-MM-DD HH:mm:ss')
      this.showCalc = true;
    },
    handleGemCalc(v) {
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: "",})
        this.logMsg.push("");
        return;        
      }
      let quantity = '';
      let gem = '';
      if (workid === "RUBY") {
        quantity = accDiv(multiple*round, this.price[0]).toFixed(4) + ' ' + 'USDC'
        gem = multiple.toFixed(4) + ' ' + workid;
      } else if (workid === "BERYL") {
        quantity = accDiv(multiple*round, this.price[1]).toFixed(4) + ' ' + 'DFG'
        gem = multiple.toFixed(4) + ' ' + workid
      } else if (workid === "TOPAZ") {
        quantity = accDiv(multiple*round, this.price[2]).toFixed(8) + ' ' + 'TAG'
        gem = multiple.toFixed(4) + ' ' + workid
      }
      this.requireAsset = quantity;
      this.createAsset = gem;
      this.requireTime = multiple * round * 120;
      this.endTime = moment().add(this.requireTime, 'm').format('YYYY-MM-DD HH:mm:ss')
      this.showCalc = true;
    },
    handleProduce(v) {
      const id = v.id;
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: ""})
        this.logMsg.push("");
        return;        
      }
      if (this.works[workid-1].id != workid) {
        this.$toast({type: 'fail', message: "id"})
        this.logMsg.push("id");
        return;             
      }
      if (v.task.exist === true) {
        this.$toast({type: 'fail', message: ""})
        this.logMsg.push("");
        return;        
      }

      let requireAsset = this.works[workid-1].require_asset.slice(); //
      requireAsset.forEach((elem, i) => {
        let asset = elem.split(" ");
        asset[0] = (parseInt(asset[0]) * multiple * round).toFixed(4);
        requireAsset[i] = asset[0] + ' ' + asset[1]
      })

      const name = this.account.name;
      const permission = this.account.permissions;
      const params = {
        actions: []
      }
      //action
      //1. bloksTransactions
      //2. bloksActions
      //3. bloksTraces
      requireAsset.forEach(elem => {
        params.actions.push({
          account: 'dfsfarmitems',
          name: 'transfer',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            from: name,
            to: 'dfsmetaverse',
            quantity: elem,
            memo: 'deposit',
          }          
        });
      })
      params.actions.push({
        account: 'dfsmetaverse',
        name: 'produce2',
        authorization: [{
          actor: name,
          permission,
        }],
        data: {
          assetid: id,
          multiple: multiple,
          round: round,
          user: name,
          workid: workid,
        }
      });
      DApp.toTransaction(params, (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleProduce tx error");
          this.logMsg.push(err.message);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleProduce tx error");
          this.logMsg.push(err.message);
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        if (!v.autoSwitch) {
          setTimeout(this.handleUpdateTask, this.updateDelay, v);
        }
      })      
    },
    async handleGetPrice() {
      const {status, result} = await this.$api.get_price();
      if (!status) {
        this.$toast({type: 'fail', message: ""})
        this.logMsg.push("");
        return
      }
      this.price = [];
      result.forEach(elem => {
        if (elem.coin === "USDC") {
          this.price[0] = elem.price;
        } else if (elem.coin === "DFG") {
          this.price[1] = elem.price;
        } else if (elem.coin === "TAG") {
          this.price[2] = elem.price;
        }
      })
    },
    async handleMakeGem(v) {
      const id = v.id;
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: "",})
        this.logMsg.push("");
        return;        
      }
      if (v.task.exist === true) {
        this.$toast({type: 'fail', message: "",})
        this.logMsg.push("");
        return;        
      }
      await this.handleGetPrice();
      const name = this.account.name;
      const permission = this.account.permissions;
      let quantity = '';
      let gem = '';
      let contract = '';
      if (workid === "RUBY") {
        quantity = accDiv(multiple*round, this.price[0]).toFixed(4) + ' ' + 'USDC'
        gem = multiple.toFixed(4) + ' ' + workid;
        contract = 'usdxusdxusdx'
      } else if (workid === "BERYL") {
        quantity = accDiv(multiple*round, this.price[1]).toFixed(4) + ' ' + 'DFG'
        gem = multiple.toFixed(4) + ' ' + workid
        contract = 'dfxtokenmain'
      } else if (workid === "TOPAZ") {
        quantity = accDiv(multiple*round, this.price[2]).toFixed(8) + ' ' + 'TAG'
        gem = multiple.toFixed(4) + ' ' + workid
        contract = 'tagtokenmain'
      }

      const params = {
        actions: [{
          account: contract,
          name: 'transfer',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            from: name,
            to: 'dfsmetaverse',
            quantity: quantity,
            memo: 'deposit',
          }          
        },{
          account: 'dfsmetaverse',
          name: 'makegem2',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            user: name,
            assetid: id,
            gem: gem,
            round: round,
          }          
        }]
      }
      DApp.toTransaction(params, (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleMakeGem tx error");
          this.logMsg.push(err.message);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleMakeGem tx error");
          this.logMsg.push(err.message);
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        if (!v.autoSwitch) {
          setTimeout(this.handleUpdateTask, this.updateDelay, v);
        }
      }) 
    },
    handleFinish(v) {
      if (v.task.exist) {
        const mature_time = moment(v.task.mature_time);
        const countDown = mature_time.diff(moment());
        if (countDown > 0) {
          v.task.countDown = countDown;
          this.$refs['#'+v.id][0].start();
          console.log("start")
          return;
        }
      }
      if (v.autoSwitch) {
        if (v.task.exist) {
          setTimeout(this.handleAutoHarvest, this.delay, v);
        } else {
          if (v.workid === 'RUBY' || v.workid === 'BERYL' || v.workid === 'TOPAZ') {
            setTimeout(this.handleAutoMakeGem, this.delay, v);
          } else {
            setTimeout(this.handleAutoProduce, this.delay, v);
          }
        }
      }
    },
    handleAutoHarvest(v) {
      if (v.task.exist === false) { //harvest
        v.failCount = 0;
        if (v.workid === 'RUBY' || v.workid === 'BERYL' || v.workid === 'TOPAZ') {
          this.handleAutoMakeGem(v);
        } else {
          this.handleAutoProduce(v);
        }
        return;     
      }
      const name = this.account.name;
      const permission = this.account.permissions;
      const params = {
        actions: [{
          account: 'dfsmetaverse',
          name: 'harvest',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            taskid: v.id,
          }
        }]
      }
      DApp.toTransaction(params, async (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoHarvest tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoHarvest)
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoHarvest tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoHarvest)
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        v.failCount = 0;
        await this.handleUpdateTask(v);
        this.handleAutoHarvest(v);
      })
    },
    handleAutoProduce(v) {
      if (v.task.exist === true) { //
        v.failCount = 0;
        this.$refs['#'+v.id][0].start();
        console.log("start")
        return;     
      }
      const id = v.id;
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      let requireAsset = this.works[workid-1].require_asset.slice(); //
      requireAsset.forEach((elem, i) => {
        let asset = elem.split(" ");
        asset[0] = (parseInt(asset[0]) * multiple * round).toFixed(4);
        requireAsset[i] = asset[0] + ' ' + asset[1]
      })

      const name = this.account.name;
      const permission = this.account.permissions;
      const params = {
        actions: []
      }
      //action
      //1. bloksTransactions
      //2. bloksActions
      //3. bloksTraces
      requireAsset.forEach(elem => {
        params.actions.push({
          account: 'dfsfarmitems',
          name: 'transfer',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            from: name,
            to: 'dfsmetaverse',
            quantity: elem,
            memo: 'deposit',
          }          
        });
      })
      params.actions.push({
        account: 'dfsmetaverse',
        name: 'produce2',
        authorization: [{
          actor: name,
          permission,
        }],
        data: {
          assetid: id,
          multiple: multiple,
          round: round,
          user: name,
          workid: workid,
        }
      });
      DApp.toTransaction(params, async (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoProduce tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoProduce);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoProduce tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoProduce)
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        v.failCount = 0;
        await this.handleUpdateTask(v);
        this.handleAutoProduce(v);
      })      
    },
    async handleAutoMakeGem(v) {
      if (v.task.exist === true) { //
        v.failCount = 0;
        this.$refs['#'+v.id][0].start();
        console.log("start")
        return;     
      }
      const id = v.id;
      const multiple = v.multiple;
      const round = v.round;
      const workid = v.workid;
      await this.handleGetPrice();
      const name = this.account.name;
      const permission = this.account.permissions;
      let quantity = '';
      let gem = '';
      let contract = '';
      if (workid === "RUBY") {
        quantity = accDiv(multiple*round, this.price[0]).toFixed(4) + ' ' + 'USDC'
        gem = multiple.toFixed(4) + ' ' + workid;
        contract = 'usdxusdxusdx'
      } else if (workid === "BERYL") {
        quantity = accDiv(multiple*round, this.price[1]).toFixed(4) + ' ' + 'DFG'
        gem = multiple.toFixed(4) + ' ' + workid
        contract = 'dfxtokenmain'
      } else if (workid === "TOPAZ") {
        quantity = accDiv(multiple*round, this.price[2]).toFixed(8) + ' ' + 'TAG'
        gem = multiple.toFixed(4) + ' ' + workid
        contract = 'tagtokenmain'
      }

      const params = {
        actions: [{
          account: contract,
          name: 'transfer',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            from: name,
            to: 'dfsmetaverse',
            quantity: quantity,
            memo: 'deposit',
          }          
        },{
          account: 'dfsmetaverse',
          name: 'makegem2',
          authorization: [{
            actor: name,
            permission,
          }],
          data: {
            user: name,
            assetid: id,
            gem: gem,
            round: round,
          }          
        }]
      }
      DApp.toTransaction(params, async (err) => {
        err = this.handleDealWithError(err)
        if (err && err.code == 402) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoMakeGem tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoMakeGem);
          return;
        }
        if (err) {
          this.$toast({
            type: 'fail',
            message: err.message,
          })
          this.logMsg.push("handleAutoMakeGem tx error");
          this.logMsg.push(err.message);
          this.handleFailProcess(v, this.handleAutoMakeGem);
          return;
        }
        this.$toast({
          type: 'success',
          message: 'Success'
        })
        v.failCount = 0;
        await this.handleUpdateTask(v);
        this.handleAutoMakeGem(v);
      }) 
    },
    async handleFailProcess(v, func) {
      this.handleUpdateTask(v); //awaitsetTimeout
      v.failCount++;
      if (v.failCount > this.failMax) {
        this.logMsg.push("");
        v.failCount = 0;
        v.autoSwitch = false;
      } else {
        if (v.autoSwitch == false) {
          v.failCount = 0;
          this.logMsg.push("");
        } else {
          this.logMsg.push(""+v.failCount+"");
          setTimeout(func, this.failDelay, v);
        }
      }
    },
    handleSwitchClick(v) {
      const workid = v.workid;
      if (workid === null || typeof workid === 'undefined') {
        this.$toast({type: 'fail', message: "",})
        this.logMsg.push("");
        return;        
      }
    },
    handleSwitchCheck(v) {
      if (v.autoSwitch) {
        this.$refs['#'+v.id][0].start();
        console.log("start")
      }
    },
    handleFreeCPU() {
      this.$store.dispatch('setFreeCpu', this.freeCPU);
    },
    noSleep() {
    let noSleep = new NoSleep();
      document.addEventListener('click',
        function enableNoSleep () {
          noSleep.enable();
          document.removeEventListener('click', enableNoSleep, false);
        },
        false);      
    }
  },
   mounted() {
    this.noSleep();
	  this.handleLogin();
    this.handleFreeCPU();
    this.handleUpdateTip();
    this.handleGetPrice();
  },
}
</script>

